.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-ticker-table {
  background: -webkit-gradient(linear, left top, right top, from(#275396), to(#48c5d0));
  background: -webkit-linear-gradient(left, #275396, #48c5d0);
  background: -o-linear-gradient(left, #275396, #48c5d0);
  background: linear-gradient(to right, #275396, #48c5d0);
  text-transform: uppercase;
  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 1em;
  width: 80%;
}

.App-ticker-table--header {
  background-color: rgba(255,255,255,0.3);
  width: 100%;
}

.App-ticker-table--header > thead > tr > th {
  width: 1%;
  padding: 10px;
  text-align: left;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
}

.App-ticker-table--header > div {
  width: 1%;
  text-align: left;
}

.App-ticker-table--body {
  width: 100%;
  text-align: left;
}

.App-ticker-table--body > tbody > tr > td {
  padding: 10px 15px;
  text-align: left;
  vertical-align: middle;
  font-weight: 300;
  font-size: 12px;
  color: #fff;
  width: 1%;
  border-bottom: solid 1px rgba(255,255,255,0.1);
}

.App-ticker-table--cell-white {
  background: -webkit-gradient(linear, left top, right top, from(#DED7D7), to(#E9E9E9));
  background: -webkit-linear-gradient(left, #DED7D7, #E9E9E9);
  background: -o-linear-gradient(left, #DED7D7, #E9E9E9);
  background: linear-gradient(to right, #DED7D7, #E9E9E9);
}
.App-ticker-table--cell-red {
  background: -webkit-gradient(linear, left top, right top, from(#e61313), to(#9c0000));
  background: -webkit-linear-gradient(left, #e61313, #9c0000);
  background: -o-linear-gradient(left, #e61313, #9c0000);
  background: linear-gradient(to right, #e61313, #9c0000);
}
.App-ticker-table--cell-green {
  background: -webkit-gradient(linear, left top, right top, from(#009F03), to(#79FF87));
  background: -webkit-linear-gradient(left, #009F03, #79FF87);
  background: -o-linear-gradient(left, #009F03, #79FF87);
  background: linear-gradient(to right, #009F03, #79FF87);
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

